#wrapper-endorsed-footer {
    .navbar-brand {
        display: none;
    } 
    .title {
        display: flex;
        align-items: center;
        
        h1 {
            color: #fff;
            }
    }

    @media (min-width: 993px) {
        .title-subunit-name {
            font-size: 1rem;
            display: block;
            width: 200px;
            text-transform: uppercase;
            font-weight: 300;
            border-left: 8px solid #F3763E;
            padding-left: 10px;
        }
    }
}
#wrapper-footer-columns {
    background: linear-gradient(45deg, #ED7440, #B96181);

    #footer-columns .flex-footer .accordion-item {
        background: unset;

        .accordion-header, .accordion-body {
            background: unset;
        }
    }
}